@import "https://fonts.googleapis.com/css2?family=Great+Vibes&family=Poppins:wght@300;500;700&display=swap";
:root {
  --bg1: #ffd1de;
  --bg2: #ffb6d5;
  --accent: #ff7a74;
  --accent-2: #ff8f81;
  --muted: #fff9;
}

html, body {
  color: #5a2b2b;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffd1de;
  height: 100%;
  margin: 0;
  font-family: Poppins, system-ui, -apple-system, Segoe UI, Roboto, Helvetica Neue, Arial;
}

.balloons {
  pointer-events: none;
  position: absolute;
  inset: 0;
  overflow: visible;
}

.balloon {
  opacity: .9;
  font-size: 46px;
  animation: 6s ease-in-out forwards balloonUp;
  position: absolute;
  bottom: -40px;
}

@keyframes balloonUp {
  0% {
    opacity: 0;
    transform: translateY(40px)scale(.9);
  }

  10% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: translateY(-380px)scale(1.1);
  }
}

.hero {
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  padding: 6rem 1.5rem;
  display: flex;
  position: relative;
  overflow: visible;
}

.hero-inner {
  text-align: center;
  z-index: 10;
  background: none;
  max-width: 900px;
  padding: 2rem;
  position: relative;
}

.title {
  color: #5b1e2a;
  letter-spacing: 1px;
  text-shadow: 0 6px 18px #e68ca040;
  margin: 0;
  font-family: Great Vibes, cursive;
  font-size: 4rem;
}

.title .spark {
  margin-left: 8px;
  font-size: .9em;
  transform: translateY(-4px);
}

.subtitle {
  color: #7a4a52;
  opacity: .95;
  margin-top: 1rem;
  font-size: 1.125rem;
}

.cta {
  background: linear-gradient(180deg, var(--accent) 0%, var(--accent-2) 100%);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 999px;
  margin-top: 2.5rem;
  padding: .85rem 2.25rem;
  font-size: 1rem;
  font-weight: 600;
  transition: transform .18s, box-shadow .18s;
  box-shadow: 0 10px 30px #ff6e6e2e;
}

.cta:active {
  transform: translateY(1px)scale(.997);
}

.cta:hover {
  box-shadow: 0 16px 40px #ff6e6e3d;
}

.floating {
  pointer-events: none;
  position: absolute;
  inset: 0;
}

.heart, .butterfly {
  opacity: .95;
  text-shadow: 0 6px 18px #0000000f;
  font-size: 2.1rem;
  position: absolute;
}

.h1 {
  transform-origin: center;
  animation: 6s ease-in-out infinite floatUp;
  top: 38%;
  left: 22%;
}

.h2 {
  font-size: 2.6rem;
  animation: 7.2s ease-in-out .6s infinite floatUp;
  top: 48%;
  left: 50%;
}

.h3 {
  font-size: 1.9rem;
  animation: 5.4s ease-in-out 1s infinite floatUp;
  top: 33%;
  left: 70%;
}

.b1 {
  font-size: 1.6rem;
  animation: 8s ease-in-out .2s infinite flutter;
  top: 55%;
  left: 12%;
  transform: rotate(-10deg);
}

.b2 {
  font-size: 1.4rem;
  animation: 7s ease-in-out .9s infinite flutter;
  top: 60%;
  left: 82%;
  transform: rotate(12deg);
}

@media (width <= 600px) {
  .title {
    font-size: 2.4rem;
  }

  .subtitle {
    padding: 0 1rem;
    font-size: 1rem;
  }

  .h1, .h2, .h3, .b1, .b2 {
    font-size: 1.6rem;
  }

  .hero {
    padding: 3.5rem 1rem;
  }
}

@keyframes floatUp {
  0% {
    opacity: .95;
    transform: translateY(6px)scale(1);
  }

  50% {
    opacity: 1;
    transform: translateY(-18px)scale(1.06);
  }

  100% {
    opacity: .95;
    transform: translateY(6px)scale(1);
  }
}

@keyframes flutter {
  0% {
    opacity: .95;
    transform: translateY(0)rotate(-10deg)scale(1);
  }

  25% {
    opacity: 1;
    transform: translateY(-12px)rotate(-2deg)scale(1.02);
  }

  50% {
    opacity: .95;
    transform: translateY(6px)rotate(6deg)scale(1);
  }

  75% {
    opacity: 1;
    transform: translateY(-6px)rotate(-4deg)scale(1.01);
  }

  100% {
    opacity: .95;
    transform: translateY(0)rotate(-10deg)scale(1);
  }
}

.frames {
  pointer-events: none;
  z-index: 1;
  position: absolute;
  inset: 0;
}

.frames-top {
  box-sizing: border-box;
  justify-content: space-between;
  gap: 18px;
  width: calc(100% - 120px);
  max-width: 1100px;
  padding: 0 12px;
  display: flex;
  position: absolute;
  top: 18px;
  left: 50%;
  transform: translateX(-50%);
}

.frames-bottom {
  box-sizing: border-box;
  justify-content: space-between;
  gap: 18px;
  width: calc(100% - 120px);
  max-width: 1100px;
  padding: 0 12px;
  display: flex;
  position: absolute;
  bottom: 18px;
  left: 50%;
  transform: translateX(-50%);
}

.frames-left {
  flex-direction: column;
  gap: 18px;
  padding: 4px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 18px;
  transform: translateY(-50%);
}

.frames-right {
  flex-direction: column;
  gap: 18px;
  padding: 4px;
  display: flex;
  position: absolute;
  top: 50%;
  right: 18px;
  transform: translateY(-50%);
}

.frame {
  box-sizing: border-box;
  background: #2e5aa0;
  border: 6px solid #d08fa0;
  border-radius: 6px;
  flex: none;
  width: 140px;
  height: 90px;
  padding: 0;
  overflow: hidden;
  box-shadow: 0 6px 12px #00000026;
}

.frame img {
  object-fit: cover;
  border-radius: 4px;
  width: 100%;
  height: 100%;
  display: block;
}

.hero-inner {
  z-index: 10;
  background: none;
}

@media (width <= 920px) {
  .frame {
    width: 110px;
    height: 72px;
  }

  .frames-top, .frames-bottom {
    gap: 12px;
    width: calc(100% - 80px);
  }

  .frames-left, .frames-right {
    gap: 12px;
    left: 10px;
    right: 10px;
  }
}

@media (width <= 520px) {
  .frames-left, .frames-right {
    display: none;
  }

  .frames-top, .frames-bottom {
    gap: 10px;
    width: calc(100% - 40px);
  }

  .frame {
    border: 5px solid #d8aab6;
    width: 80px;
    height: 54px;
  }
}
